export default {
	data() {
		return {
			discounts: "无",
			showCards: false,
			cards: ['-1', "-2", "-3"],
			index: 0,
			allCount: 200,
			order_id:300,
			goodsList:[],
			items:[{
				value:'man',
				name:'先生'
			},
			{
				value:'woman',
				name:'女士'
			}],
			pics:[-1,-2,-3],
			time:0,
			num:0,
			shopname:'店名',
			username:'',
			userphone:'',
			des:'',
			current:0,
			payitems:[
				{
					value:'weixin',
					name:'微信支付'
				},
				{
					value:'zhifubao',
					name:'支付宝支付'
				}
			],
			paycurrent:0,
			paytype:''
			
		}
	},
	methods: {
		async requestPayment(type) {
			// 提交订单到后台商家
			if(type=='weixin'){
				
			}
			var that = this
			if (parseFloat(this.realCount) != 0.00 && parseFloat(this.realCount) != 0) {
				console.log('正在支付')
				uni.showLoading({
					title: '正在支付...'
				});
				// 传入price,body,订单号（随机生成）
				// e.id为支付通道
				let orderId = '';
				orderId = this.randomOrder(orderId)
				console.log(orderId)
				let price = this.realCount
				let body = "莫等闲-预定支付";
				let orderInfo1 = await this.getOrderInfo(orderId, price, body);
				// console.log(orderInfo1)


				if (!orderInfo1.appid) {
					uni.showToast({
						title: '支付信息有误',
						icon: 'none'
					});
					uni.hideLoading();
					return false;
				}
				// 生成签名支付签名算法
				var obj = {
					appid: orderInfo1.appid, //id 应用id
					partnerid: orderInfo1.partnerid, //商户号 
					prepayid: orderInfo1.prepayid, //预支付
					package: 'Sign=WXPay',
					noncestr: orderInfo1.noncestr,
					timestamp: orderInfo1.timestamp, //时间戳
					sign: orderInfo1.sign
				}
				var obj2 = {
					appid: orderInfo2.appid, //id 应用id
					partnerid: orderInfo2.partnerid, //商户号 
					prepayid: orderInfo2.prepayid, //预支付
					package: 'Sign=AliPay',
					noncestr: orderInfo2.noncestr,
					timestamp: orderInfo2.timestamp, //时间戳
					sign: orderInfo2.sign
				}
				let orderInfo = JSON.stringify(obj)
				uni.requestPayment({
					provider: 'wxpay',
					orderInfo: orderInfo,
					success: (e) => {
						// that.dddd = e
						uni.request({
							url: this.$bashUrl + '/index.php/home/Api/user_order/',
							method: 'POST',
							data: {
								id: this.order_id,
								price: this.realCount,
								goods: this.goodsList,
								pay_state: 1
							},
							success(res) {
								uni.showToast({
									title: "支付成功!",
									icon: "none"
								})
								uni.hideLoading();
								// 成功后跳转到tab页面
								setTimeout(function() {
									uni.switchTab({
										url: '/pages/reserve/reserve'
									});
								}, 2000);
							},
							fail(err) {
								uni.hideLoading();
								console.log(err)
							}
						})
					},
					fail: (e) => {

						uni.hideLoading();
					},
					complete: (ok) => {
						console.log(ok)
						uni.navigateBack({
							delta:1
						})
					}
				})
			} else {
				uni.showToast({
					title: '订单不能为空',
					icon: 'none'
				});
			}
		},
		randomOrder(value) {
			for (var i = 0; i < 6; i++) //6位随机数，用以加在时间戳后面。
			{
				value += Math.floor(Math.random() * 10);
			}
			return new Date().getTime() + value; //时间戳，用来生成订单号。
		},
		getOrderInfo(orderId, price, body) {
			let appid = "";



			let url = 'https://ww.0816fc.net/index.php/home/Pay/pay'
			return new Promise((resolve) => {
				uni.request({
					url: url,
					method: "POST",
					data: {
						id: this.order_id,//订单号
						orderId: orderId,//随机生成的order
						body: body,//文字内容
						price: price,//价格
						goods: this.goodsList,//商品列表
					},
					success: (result) => {
						console.log(result)
						resolve(result.data)
					},
					fail: (e) => {
						resolve(e);
					}
				})
			})
		},
		chosecards(e) {
			console.log(e)
			this.index = e.target.value
		},
		radioChange(e){
			console.log(e)
			   for (let i = 0; i < this.items.length; i++) {
			                if (this.items[i].value === e.target.value) {
			                    this.current = i;
			                    break;
			                }
			            }
		},
		payChange(e){
			for (let i = 0; i < this.items.length; i++) {
			             if (this.items[i].value === e.target.value) {
			                 this.paycurrent = i;
			                 break;
			             }
			         }
			this.paytype = e.target.value
		}
		
	},
computed:{
	realCount(){
		// return this.allCount - Math.abs(this.cards[this.index])
		return this.allCount 
	}
},
	onLoad(e) {
		this.order_id = e.order_id
		this.allCount = e.price
		this.goodsList = e.goodslist.split(',')
		console.log(this.goodsList)
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/my_order_info/',
			method: 'POST',
			data: {
				good_id: this.order_id
			},
			success:res=>{
				console.log(res)
				this.time=res.data.infos.point_time
				this.num = res.data.infos.num
				this.shopname = res.data.store_name
			}
			})
	}
}
